<template>
  <div>
    <div>

      <div style="display:flex;justify-content: center; flex-wrap: wrap;">
        <div v-for="(item,index) in onlineResources" :key="index">
          <el-card class="card-style"
                   :title="'点击前往 ' + item.name">
            <el-link :href="item.url" target="_blank">
              <div style="display: flex;">
                {{ index + 1 }}.
                <div>
                  <img :src="item.icon" width="50" style="border-radius: 50px" alt="">
                </div>
                <div style="margin-left: 6px">
                  <div style="display: flex">
                                        <span style="color: darkmagenta;font-family: 华文宋体">
                                            {{ item.name }}
                                        </span>
                    <span>
                                            <span v-for="(itemTag,indexTag) in item.label.toString().split(';')"
                                                  :key="indexTag">
                                            <el-tag :type="getType(indexTag)" style="margin-left: 3px">{{
                                                itemTag
                                              }}</el-tag>
                                                </span>
                                        </span>
                  </div>
                  <div style="margin-top: 4px;">
                    {{ item.remark }}
                  </div>
                </div>
              </div>
            </el-link>
          </el-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>

import {ref} from "vue";

//读取影视数据
const onlineResources = ref(require('./onlineResources.json'))

//返回标签类型
const getType = (index) => {
  switch (index) {
    case 0:
      return 'warning';
    case 1:
      return 'danger';
    case 2:
      return 'success';
    default:
      return 'info'
  }
}

</script>

<style scoped>
.card-style {
  background-color: rgba(255, 255, 255, 0.2);
  min-width: 10%;
  min-height: 10%;
  margin: 10px 10px 10px auto;
  margin-top: 18px;
  border-radius: 10px
}
</style>